
import React from 'react';
import YouTube from 'react-youtube';

export const Videob2 = () => {
  const videoId = 'eFbsOdIzj7w'; // Reemplaza esto con el ID de tu video de YouTube

  const opts = {
    height: '300',
    width: '600',
    playerVars: {
      autoplay: 0,
    },
  };

  return (
    <div>
      <YouTube videoId={videoId} opts={opts} />
    </div>
  );
}




