
import { Col, Container, Row } from "react-bootstrap"
import React from 'react';
import YouTube from 'react-youtube';
import { Grammy } from "../VideoPremio/Grammy";
import {Latin} from "../VideoPremio/Latin";

 const Premios = () =>{
    return(
        <section  className="banner" id ="canciones">
        <Container>
            <Row className="aling-intems-center">
            <h1>{'SEXTA GENERACION'} <span className="wrap"> </span></h1>                  
                <Col xs={12} md={6} xl={7}>
              <br/>
  
                    <p>La sexta generación se podría llamar a la era de las computadoras inteligentes basadas en redes neuronales artificiales o "cerebros artificiales". Serían computadoras que utilizarían superconductores como materia-prima para sus procesadores, lo cual permitirían no malgastar electricidad en calor debido a su nula resistencia, ganando rendimiento y economizando energía. La ganancia de rendimiento sería de aproximadamente 30 veces la de un procesador de misma frecuencia que utilice metales comunes.  </p>
           </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
               <Latin/>
            </Col>
          <Col xs={12} md={6} xl={7}>
             <br/>  
            <span className="tagline">Grammy</span> 
        
        <p>Todo esto está en pleno desarrollo, por el momento las únicas novedades han sido el uso de procesadores en paralelo, o sea, la división de tareas en múltiples unidades de procesamiento operando simultáneamente. Otra novedad es la incorporación de chips de procesadores especializados en las tareas de vídeo y sonido. </p>
       </Col>
          <br/>  
        <Col xs={12} md={6} xl={5}>
          <br/>  
               <Grammy/>
        </Col>
   
           </Row>
        </Container>
        </section>
   
    );
}
export default Premios;
 