
import { Col, Container, Row } from "react-bootstrap"
import React from 'react';
import YouTube from 'react-youtube';
import { Lucha } from "../VideoPremio/Lucha";
import imgLucha from "../MiImagenes/image.jpg";

const LuchaLibre = () =>{
    return(
        <section  className="banner" id ="canciones">
        <Container>
            <Row className="aling-intems-center">
            <h1>{'La inteligencia Artificial'} <span className="wrap"> </span></h1>                  
                <Col xs={12} md={6} xl={7}>
              <br/>
                 <span className="tagline">Incios</span> 
                    <p>La inteligencia artificial (IA) es una rama de la informática que desarrolla programas capaces de emular procesos propios de la inteligencia humana. Es decir, las máquinas pueden analizar el entorno y realizar determinadas acciones de manera más o menos autónoma con el fin de lograr objetivos concretos</p>
           </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
               <Lucha/>
            </Col>
          <Col xs={12} md={6} xl={7}>
             <br/>  
            <span className="tagline">Continuamos al futuro</span> 
        
        <p>A pesar de su auge actual, no se trata de un concepto nuevo: en la década de los 50 del siglo pasado, Alan Turing ya planteó si las máquinas serían capaces de pensar. De ahí surgió, lo que ahora se conoce como “el test de Turing”, que se utiliza para determinar si una computadora puede ser tan inteligente como un ser humano. Consiste en exponer a un humano a dos conversaciones, una realizada por una máquina y la otra por un ser humano. Si la persona no es capaz de distinguir cuál es la máquina, se sobreentiende que esta es tan inteligente como el humano. En 1956, John MacCarthy fue el primero en pronunciar la expresión «inteligencia artificial» en la Conferencia de Darthmouth. </p>
        </Col>
          <br/>  
        <Col xs={12} md={6} xl={5}>
          <br/>  
          <img src= {imgLucha} alt="Header"/>

        </Col>
   
           </Row>
        </Container>
        </section>
   
    );
}
 
export default LuchaLibre;