
import React from 'react';
import YouTube from 'react-youtube';

export const Latin = () => {
  const videoId = '9kAxrPG5XT4'; // Reemplaza esto con el ID de tu video de YouTube

  const opts = {
    height: '250',
    width: '650',
    playerVars: {
      autoplay: 0,
    },
  };

  return (
    <div>
      <YouTube videoId={videoId} opts={opts} />
    </div>
  );
}




