import React from 'react';
import YouTube from 'react-youtube';

export const ActorC = () => {
  const videoId = 'FdZ8LKiJBhQ'; // Reemplaza esto con el ID de tu video de YouTube

  const opts = {
    height: '250',
    width: '650',
    playerVars: {
      autoplay: 0,
    },
  };
  
  return (
    <div>
      <YouTube videoId={videoId} opts={opts} />
    </div>
  );
}




