
import { Col, Container, Row } from "react-bootstrap"
import { useEffect,useState } from "react";
import React from 'react';
import YouTube from 'react-youtube';
import { VideoPlayer } from "../MisVideos/VideoPlayer";
import {Videob2} from "../MisVideos/Videob2";
import {Videob3} from "../MisVideos/Videob3";
import {Videob4} from "../MisVideos/Videob4";
import {Videob5} from "../MisVideos/Videob5";

 const Canciones = () =>{
    return(
        <section  className="banner" id ="canciones">
        <Container>
            <Row className="aling-intems-center">
            <h1>{'3ra Generacion de Computadoras'} <span className="wrap"> </span></h1>  
                <Col xs={12} md={6} xl={7}>
              <br/>
                 <span className="tagline">Primero</span> 
                    <p>En la década del 60, el desarrollo de los circuitos integrados fue la marca de la tercera generación.  Los transistores fueron minimizados y puestos en placas de silicón, llamados semiconductores, los cuales incrementaron drásticamente la velocidad y eficiencia de los computadores.  </p>
<br/>
 </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
               <VideoPlayer/>
            </Col>
    <Col xs={12} md={6} xl={7}>
    <br/>  
            <span className="tagline">Segundo </span> 
          <p>
          El Burroughs B-2500 fue uno de los primero y estos podían almacenar millones de números. Surgen conceptos como memoria virtual, multiprogramación y sistemas operacionales complejos. Ejemplos de esta época son el IBM 360 y el BURROUGHS B-3500.
          </p>
          </Col>
          <br/>  
          <Col xs={12} md={6} xl={5}>
          <br/>  
               <Videob2/>
   </Col>
   
   <Col xs={12} md={6} xl={7}>
   <br/>  
            <span className="tagline">Tercero</span> 
                   <br/>
       <p>1959 con un capital de 100 dólares, se transformó en la primera compañía de software con acciones negociadas en bolsa. El primer mini computador comercial surgió en 1965, el PDP-5, dependiendo de su configuración y accesorios él podía ser adquirido por US$ 18,000.00. Le siguió el PDP-8, de precio más competitivo. Siguiendo su camino otras compañías lanzaron sus modelos, haciendo que a finales de la década ya existieran cerca de 100.000 computadores esparcidos por el mundo. En 1970 INTEL introdujo en el mercado un nuevo tipo de circuito integrado: el microprocesador. </p>  
     </Col>

    <Col xs={12} md={6} xl={5}>
          <br/>  
               <Videob3/>
     </Col>

     <Col xs={12} md={10} xl={6}>
   <br/>
           <Videob4/>  
             </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
          <span className="tagline">Cuarto  </span> 
                   <br/>
       <p>Amorfoda quizás es uno de los temas más controversiales de todo el repertorio de Bad Bunny, aún hoy en día muchos critican o cuestionan la calidad de esta canción. La verdad del caso, es que pocas veces habíamos visto que algún intérprete del género (especialmente del trap) se permitiera un momento de vulnerabilidad como este, dejando a un lado todos los clichés que suelen escucharse en las letras de estos artistas, para que los sentimientos tomen el rol protagónico de la historia. Quizás sin quererlo, el conejo le dio un himno para todos aquellos que sencillamente no creen en el amor y que hasta el sol de hoy es de sus mejores composiciones.</p>
       
     </Col>




            </Row>
        </Container>
        </section>
   
    );
}
 
export default Canciones;