
import React from 'react';
import YouTube from 'react-youtube';

export const Videob4 = () => {
  const videoId = 'zdYMGCOcoHk'; // Reemplaza esto con el ID de tu video de YouTube

  const opts = {
    height: '300',
    width: '600',
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 0,
    },
  };

  return (
    <div>
      
      <YouTube videoId={videoId} opts={opts} />
    </div>
  );
}




