import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/CPUantigua.png";
import headerImg1 from "../assets/img/CPUnueva.png";
import { ArrowRightCircle } from "react-bootstrap-icons";

import TrackVisibility from 'react-on-screen';

 const Baner = () =>{

//////////  
return(
    <section className="banner" id="home">
    <Container>
        <Row className="aligh-items-center">
        <Col xs={6} md={3} xl={3}>
            <TrackVisibility>
            {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                <img src={headerImg} alt="Header Img"/>
                </div>}
            </TrackVisibility>
        </Col>
        <Col xs={10} md={5} xl={6}>
            <TrackVisibility>
            {({ isVisible }) =>
            <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="tagline">Generaciones de Computadoras</span>
                <p>Cada generación de computadoras es caracterizada por un mayor desarrollo tecnológico que fundamentalmente cambia la forma en que las computadoras operan, dando como resultado productos más pequeños, baratos, poderosos, eficientes y confiables. </p>
               
            </div>}
            </TrackVisibility>
        </Col>
        <Col xs={6} md={3} xl={3}>
            <TrackVisibility>
            {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                <img src={headerImg1} alt="Header Img"/>
                </div>}
            </TrackVisibility>
        </Col>
        </Row>
        <Row className="aligh-items-center banner-abajo">
        
            <Col className="ba-col" xs={6} md={3} xl={3}>
            <button onClick={() => console.log('connect')}>Primera Generacion <ArrowRightCircle size={25} /></button>
            </Col>
            <Col className="ba-col" xs={10} md={5} xl={6}>
            
            </Col>
            <Col className="ba-col" xs={6} md={3} xl={3}>
            <button onClick={() => console.log('connect')}>Sexta Generacion<ArrowRightCircle size={25} /></button>
            </Col>
        </Row>
    </Container>
</section>
  );
  
 };
  
 export default Baner;
 //tengo hacer un cantante favorito 



