import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Outlet , Link} from 'react-router-dom';
import logo from '../MiImagenes/LogoPC-1.png'


function NavbarOficial() {
  
  return (
    <>
    <Nav className='he'>
        </Nav>
    <Navbar expand="lg" className="bg" variant='dark'>
      <Container>
        <Navbar.Brand as={Link} to="/">
            <img className='logoP' src={logo} alt="Logo"/>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/Baner">INICIO</Nav.Link>
            <Nav.Link as={Link} to="/Formulario">1RA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/Biografia">2DA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/Canciones">3RA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/Carousel">4TA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/Colaboracion">5TA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/Premios">6TA GENERACION</Nav.Link>
            <Nav.Link as={Link} to="/LuchaLibre">I.A.</Nav.Link>
            <Nav.Link as={Link} to="/Actor">OTROS</Nav.Link>
            <Nav.Link as={Link} to="/Creditos">CREDITOS</Nav.Link>

          </Nav>
        </Navbar.Collapse>
        
      </Container>
    </Navbar>

    <section>
        <Outlet></Outlet>
    </section>
    </>
  );
}

export default NavbarOficial;

