
import { Col, Container, Row } from "react-bootstrap"
import { useEffect,useState } from "react";
import React from 'react';
import YouTube from 'react-youtube';
import becki from "../MiImagenes/Pentium_I.jpg";
import Ozuna from "../MiImagenes/Pentium_II.jpg";
import Maluma from "../MiImagenes/Pentium_III.jpg";
import Natti from "../MiImagenes/PENTIUM 4.webp";



const Colaboracion = () =>{
  
    return(
        <section  className="colabora" id ="canciones">
        <Container>
            <Row className="aling-intems-center">
            <h1>{'LAS MEJORES COLABORACIONES'} <span className="wrap"> </span></h1>  
                <Col xs={12} md={6} xl={7}>
              <br/>
                 <span className="tagline">Pentium I</span> 
                  <p>Las aplicaciones exigen cada vez más una mayor capacidad de procesamiento y almacenamiento de datos. Sistemas especiales, sistemas multimedia, bases de datos distribuidas y redes neuronales, son sólo algunos ejemplos de esas necesidades. Una de las principales características de esta generación es la simplificación y miniaturización del computador, además de mejor desempeño y mayor capacidad de almacenamiento. </p>
<br/>
 </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
          <img src= {becki} alt="Header"/>
            </Col>
            
    <Col xs={12} md={6} xl={7}>
    <br/>  
            <span className="tagline">Pentium II</span> 
       <p> El concepto de procesamiento está yendo hacia los procesadores paralelos, o sea, la ejecución de muchas operaciones simultáneamente por las máquinas. La reducción de los costos de producción y del volumen de los componentes permitió la aplicación de estos computadores en los llamados sistemas embutidos, que controlan aeronaves, embarcaciones, automóviles y computadores de pequeño porte. </p>
        </Col>
          <br/>  
          <Col xs={12} md={6} xl={5}>
          <br/>  
          <img src= {Ozuna} alt="Header"/>
   </Col>
   
   <Col xs={12} md={6} xl={7}>
   <br/>  
            <span className="tagline">Pentium III </span> 
                   <br/>
         <p> La última etapa de la quinta generación de computadoras fue anunciada como la de las "computadoras inteligentes", basadas en Inteligencia Artificial, iniciada por un famoso proyecto en Japón, y que finalizó en un estrepitoso fracaso; a partir de ahí, la cuenta de las generaciones de computadoras es un poco confusa. </p>
            </Col>

    <Col xs={12} md={6} xl={5}>
          <br/>  
          <img src= {Maluma} alt="Header" id="ma" style={{ width: '450px', height: '400px' }}/>
     </Col>

     <Col xs={12} md={10} xl={6}>
   <br/>
   <img src= {Natti} alt="Header"/>
             </Col>
          <Col xs={12} md={6} xl={5}>
          <br/>  
          <span className="tagline">Pentium IV  </span> 
                   <br/>
   <p> Hubo también un intento japonés por construir una FGCS (Fifth Generation Computer Systems, Sistemas Computarizados de Quinta Generación) que sería un nuevo diseño de computadores fuertemente basados en la inteligencia artificial. Sin embargo, luego de once años de desarrollo, el proyecto no dio los resultados esperados.</p>
     </Col>



            </Row>
        </Container>
        </section>
   
    );
}
 
export default Colaboracion;