
import { Col, Container, Row } from "react-bootstrap"
import headerImg from "../MiImagenes/segunda-generacion.jpg";
import { ArrowRightCircle } from "react-bootstrap-icons";
//efecto que desaparece el nombre y aparce

const Biografia = () =>{

 return(

  <section  className="banner" id ="historia">
        <Container>
            <Row className="aling-intems-center">
                <Col xs={12} md={6} xl={7}>
                <h1>{'2da Generacion' } <span className="wrap"> </span></h1>
                 <br/>
<p>Desde 1956 hasta 1963 se mantuvo vigente la segunda generación de computadoras que llegó con la invención de los transistores. Esto supuso el reemplazo de los tubos de vacío y un avance importante en el mundo de la computación. Un transistor es un dispositivo que sirve como regulador de la corriente eléctrica, lo que permitió la creación de computadoras más eficientes en términos energéticos.</p>

<span className="tagline">CARRERA</span>
                 <br/>
    <p>
    Ejemplos de esta época son el IBM 1401 y el BURROUGHS B 200. En 1954 IBM comercializa el 650, de tamaño medio. El primer computador totalmente transistorizado fue el TRADIC, de laboratorios Bell. El IBM TX-0, de 1958, tenía un monitor de vídeo de primera calidad, era rápido y relativamente pequeño, poseía dispositivo de salida sonora. El PDP-1, procesador de datos programable, construido por Olsen, fue una sensación en el MIT: los alumnos jugaban Spacewar y Ratón en el laberinto, a través de un joystick y un lápiz óptico.  
   </p>
   <br/><br/>
   <span className="tagline">CONEJO MALO</span>
   <p>
   Numerator Integrator and Computer. En enero de 1959  se anuncia al mundo una creación de Jack Kilby: el circuito integrado. Mientras a una persona de nivel medio le llevaría cerca de cinco minutos multiplicar dos números de diez dígitos, MARK I lo hacía en cinco segundos, el ENIAC en dos milésimas de segundo, un computador transistorizado en cerca de cuatro billonésimas de segundo, y, una máquina de tercera generación en menos tiempo aún.  
   </p>
   <br/><br/>
   <span className="tagline">ARTE</span>
   <p>Uno de los modelos más conocidos de esta generación fue la IBM 1401 Mainframe. Era una máquina voluminosa y costosa que aún leía tarjetas perforadas, pero que era tan exitosa que se vendieron 12.000 unidades, todo un éxito de mercado para el momento (1959).
Por otro lado, se destacó la System/360, también de IBM, de la que se vendieron 14.000 unidades en 1968, perteneciente a toda una gama de modelos bastante exitosos para uso individua
</p> 
     <br/><br/>
      </Col>
                <Col xs={20} md={10} xl={5}>
                <img src= {headerImg} alt="Header"/>
                </Col>
            </Row>
        </Container>
    </section>

  

 );
}

export default Biografia;


