
import { Col, Container, Row } from "react-bootstrap"
import React from 'react';
import YouTube from 'react-youtube';
import { ActorC } from "../VideoPremio/ActorC";
import imgActor from "../MiImagenes/ChatGPT.jpeg";

 const Actor = () =>{
    return(
        <section  className="banner" id ="canciones">
        <Container>
            <Row className="aling-intems-center">
            <h1 className="h1-Actor" >{'CHAT GPT'} <span className="wrap"> </span></h1>                  
                <Col xs={12} md={6} xl={7}>
              <br/>
                <span className="tagline">Comencemos</span> 
                   <p>Se trata de una inteligencia artificial que está entrenada para mantener conversaciones, de manera que solo tienes que hacerle preguntas de manera convencional y las entenderá. Esto abre la puerta a las muchas funciones de ChatGPT, gran cantidad de comandos con los que usarla. Empezaremos explicándote lo que es, y luego te pondremos algunos ejemplos de lo que puedes llegar a hacer con ella.</p>
           </Col>

          <Col xs={12} md={6} xl={5}>
          <br/>  
               <img src= {imgActor} alt="Header"/>
            </Col>
          <Col xs={12} md={6} xl={7}>
             <br/>  0
            <span className="tagline">Como Funuciona</span> 
        <p>Empezar a tener tus conversaciones con testa inteligencia artificial es muy sencillo. Lo único que tienes que hacer es entrar a su web oficial, que es https://chat.openai.com. La primera vez que entres tendrás que crearte una cuenta en la web de OpenAI, pero todo es completamente gratuito, y también el chat.

Y una vez inicies sesión ya entrarás en el chat. En él, abajo del todo tendrás la barra donde tú escribes lo que quieras. Antes de empezar verás algunos ejemplos en inglés de cosas que le puedes preguntar, pero recuerda que también puedes hacerle preguntas en Español, y pedirle que te hable en español o en cualquier otro idioma que tú prefieras. Vamos, que es fácil usar ChatGPT en español.

A la hora de utilizarlo, es recomendable que experimentes con distintos tipos de peticiones para explorar todas las funciones de la IA. En casos como este, el ingenio a la hora de realizar preguntas o peticiones puede ser clave para obtener resultados sorprendentes</p>    
                </Col>
          <br/>  
        <Col xs={12} md={6} xl={5}>
          <br/>  
               <ActorC/>
        </Col>
   
           </Row>
        </Container>
        </section>
   
    );
}
export default Actor;
 

